import React from "react";

export default function (Component, step) {
    return class extends React.Component {
        state = {
            render: true
        }
        constructor(props) {
            super(props);
            props.onMount(step);
        }
        render() {
            if (!this.state.render) return null;
            return <Component stepIndex={step} {...this.props} />
        }
    }
}
