import Totales from "./Totales";

/*const URL = (window.location.href.indexOf("http://localhost") === 0)
    ? "http://localhost:10000/data/"
    : (window.location.protocol === "https:"
        ? "https://orders-runfoodapp.rj.r.appspot.com/data/"
        : "http://runfoodapp.ddns.net:10000/data/");*/
const URL = "https://orders.runfoodapp.com/data/";
//const URL = "http://localhost:10000/data/";
function store() {
    const data = window.location.pathname.split("/");
    for (let i = 0; i < data.length; i++) {
        if (data[i].indexOf("@") === 0) {
            return data[i].replace(/#/g, "").replace("@", "");
        }
    }
    return "";
}
const API = {
    GetProducts() {
        return Fetch(URL + store() + "/product");
    },
    GetProduct(ID) {
        return Fetch(URL + store() + "/product/" + ID);
    },
    SaveOrder(state) {
        let { contactData, direccion, invoiceData, items, envio_programado, delivery } = state;

        if (!contactData) contactData = {};
        if (!direccion) direccion = {};
        if (!invoiceData) invoiceData = {};

        let taking_order_time = new Date().valueOf() - state.start_timestamp;

        var all_items = [...items];
        if (delivery && delivery.valor) all_items.push({ id: "domicilio", pvp: delivery.valor, pagaIva: 0 });
        const totals = Totales(all_items, state.store.porcentajeIVA);
        totals.detalle = items;

        const ORDER = {
            ...contactData,
            nota: state.nota,
            forma_pago: state.forma_pago,
            numeroTransaccion: state.numeroTransaccion,

            lat: direccion && direccion.position && direccion.position.lat,
            lng: direccion && direccion.position && direccion.position.lng,
            direccion: direccion.direccion,
            referencia: direccion.referencia,

            envio_programado,

            valor_delivery: delivery && delivery.valor,
            mts_delivery: delivery && delivery.metros,
            segs_delivery: delivery && delivery.segundos,
            info_id: delivery && delivery.info_id,

            documento_factura: invoiceData.documento,
            identificacion_factura: invoiceData.identificacion,
            razonsocial_factura: invoiceData.razonSocial,
            direccion_factura: invoiceData.direccion,
            telefono_factura: invoiceData.telefono,
            email_factura: invoiceData.email,

            ...totals,

            taking_order_time
        }
        return Fetch(URL + store() + "/order", {
            body: JSON.stringify(ORDER),
            method: "POST",
            mode: "cors",
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
            }
        });
    },
    GetStoreData() {
        return Fetch(URL + store() + "?withgooglemapskey=1");
    },

    CheckCoverage(position) {
        return Fetch(URL + store() + `/check-coverage?lat=${position.lat}&lng=${position.lng}`);
    },
    RedirectTo(new_store, step1) {
        var url = window.location.href;
        var new_url = url.split("/@" + store())[0] + ("/@" + new_store);
        if (step1) new_url += "?step1=" + btoa(JSON.stringify(step1)) + "#/1";
        window.location.replace(new_url);
    }
}
function Fetch(URI, options) {
    const DEFAULTS = {
        method: "GET",
        cache: "reload",
        mode: "cors"
    };
    return fetch(URI, options || DEFAULTS).then(response => {
        if (response.ok) {
            return response.json();
        } else {
            return response.clone().json()
                .catch(() => {
                    return response.text().then(x => {
                        throw {
                            message: x,
                            status: response.status,
                            statusText: response.statusText
                        }
                    })
                }).then(obj => {
                    throw {
                        ...obj,
                        status: response.status,
                        statusText: response.statusText
                    }
                })
        }
    }).catch(ex => {
        if (ex.message === "Failed to fetch") {
            throw {
                message: "Este servicio no se encuentra disponible por el momento. Intentelo más tarde.",
                status: 0
            }
        }
        if (!ex.message) ex.message = ex.statusText;
        throw ex;
    });
}
export default API;