import React from "react";
import { Typography, TextField, List, ListItem, Button } from "@material-ui/core";
import { Route } from "react-router-dom";
import ProductDetails from "../ProductDetails";
import Totales from "../../Services/Totales";
import Schedule from "./Schedule";

export default class ConfirmOrder extends React.PureComponent {
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepIndex);
        this.handleClick = this.handleClick.bind(this);
        this.handleEnvio = this.handleEnvio.bind(this);
        this.programarEnvio = this.programarEnvio.bind(this);
    }
    handleClick(event) {
        const id = event.currentTarget.id;
        this.props.history.push(this.props.match.url + "/view-product/" + id);
    }
    programarEnvio(value) {
        this.props.onDataChange && this.props.onDataChange("envio_programado", value);
        this.props.history.push(this.props.match.path);
    }
    handleEnvio(event) {
        const programar_envio = event.currentTarget.value;
        if (programar_envio) {
            this.props.history.push(this.props.match.path + "/schedule");
        } else {
            this.programarEnvio();
        }
    }
    render() {
        console.log("ConfirmOrder rendered");
        const { props } = this;
        const deliveryCost = (props.delivery || {}).valor;
        const deliveryStatus = (props.delivery || {}).status;

        return (<div style={{ paddingLeft: 10, paddingRight: 10, overflow: "auto", flex: 1 }}>
            <List style={{ marginBottom: 10 }} dense={true}>
                {(props.items).map(x => generateRowItem(x, this.handleClick))}

                {(deliveryStatus === "ACTIVO" && !isNaN(+deliveryCost))
                    ? generateRowItem({
                        id: "DOMICILIO",
                        nombre: "ENVIO A DOMICILIO"/* + (props.delivery.metros
                            ? (" (" + (props.delivery.metros / 1000).round(1) + " km)")
                            : "")*/,
                        pvp_final: deliveryCost,
                        cantidad: 1
                    }) : null}

                {generateTotals(props.items, deliveryCost, props.store.porcentajeIVA)}
            </List>
            {deliveryStatus !== "ACTIVO"
                ? (<Typography variant="subtitle1" color="secondary">
                    El costo del Envío a Domicilio no está disponible.
                    Debes consultar este valor con la tienda.
                </Typography>)
                : null}

            <div style={{ width: "100%", textAlign: "center" }}>
                <TextField
                    label="Agrega una nota a tu pedido"
                    placeholder="Ej. Sin cubiertos, sin salas extra, etc."
                    multiline
                    name="nota"
                    inputProps={{ maxLength: 200 }}
                    value={props.nota}
                    onChange={props.onInputChange}
                    rows={2}
                    style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
                />
                <div style={{ width: "95%", display: "flex", flexDirection: "row", marginTop: 10 }}>
                    {props.store.estado === "ACTIVO" &&
                        (<Button color={props.envio_programado ? "default" : "primary"} style={{ flex: 1, marginRight: 5 }} variant={props.envio_programado ? "outlined" : "contained"} onClick={this.handleEnvio} value="" >
                            Entrega inmediata
                        </Button>)}
                    <Button color={props.envio_programado ? "primary" : "default"} style={{ flex: 1, marginLeft: 5 }} variant={props.envio_programado ? "contained" : "outlined"} onClick={this.handleEnvio} value="1">
                        {props.envio_programado ? "Entrega programada" : "Programa la Entrega"}
                    </Button>
                </div>
            </div>
            <Route path={props.match.path + "/view-product/:id"} >
                <ProductDetails productList={props.productList} choosenItems={props.items || []} onDone={props.onItemChange} />
            </Route>
            <Route path={props.match.path + "/schedule"} >
                <Schedule store={props.store} value={props.envio_programado} onDone={this.programarEnvio} />
            </Route>
        </div>)
    }
}

function generateRowItem(data, onClick) {
    const color = data.id === "DOMICILIO" ? "primary" : "default";
    return (
        <ListItem
            key={data.id} button={Boolean(onClick)} onClick={onClick} id={data.id}>
            <div style={{ display: "table", width: "100%" }}>
                <div style={{ display: "table-cell", paddingRight: 5 }}>
                    <Typography color={color} variant="subtitle1">{data.nombre}</Typography>
                    {data.nota && <Typography color={color} variant="body1">{data.nota}</Typography>}
                </div>
                <div style={{ display: "table-cell", width: "15%", maxWidth: 100, textAlign: "right" }}>
                    <Typography color={color} variant="h6" style={{ margin: 0, padding: 0 }}>x{data.cantidad}</Typography>
                </div>
                <div style={{ display: "table-cell", width: "25%", maxWidth: 100, minWidth: 75, textAlign: "right" }}>
                    <Typography color={color} variant="h6" style={{ margin: 0, padding: 0 }}>$ {(data.pvp_final * data.cantidad).round(2).toFixed(2)}</Typography>
                </div>
            </div>
        </ListItem>)
}
function generateTotals(items, valor_envio, porcentajeIVA) {
    var items_to_calc = [...items];
    if (valor_envio) items_to_calc.push({
        id: "DOMICILIO", cantidad: 1, pvp: valor_envio, pagaIva: false
    });
    const totals = Totales(items_to_calc, porcentajeIVA);
    return (<ListItem>

        <div style={{ display: "table", width: "100%" }}>
            <div style={{ display: "table-cell", paddingRight: 5 }}>
                <Typography variant="h6">TOTAL</Typography>
            </div>
            <div style={{ display: "table-cell", width: "15%", maxWidth: 100, textAlign: "right" }}></div>
            <div style={{ display: "table-cell", width: "25%", maxWidth: 100, minWidth: 75, textAlign: "right" }}>
                <Typography style={{ margin: 0, padding: 0 }} variant="h6">
                    $ {totals.total.round(2).toFixed(2)}
                </Typography>
            </div>
        </div>

    </ListItem>);
}
