import React from 'react';
import { Paper, Stepper, Step, StepLabel, Button, CircularProgress, Typography } from '@material-ui/core';
import { Switch, Route, withRouter, HashRouter, Redirect, Prompt } from "react-router-dom";
import {
  ReceiptOutlined, PlaceOutlined, PersonOutlined, AddShoppingCartOutlined,
  Receipt, Place, Person, ShoppingCart, MonetizationOn, MonetizationOnOutlined
} from '@material-ui/icons';

import API from "./Services/API";
import * as prefs from "./Services/Preferences";
import State from "./App.state";

import A from './Components/TakeOrder';
import B from './Components/ConfirmOrder';
import C from './Components/ContactData';
import D from './Components/Location';
import E from './Components/Payment';
import F from './Components/Done';
import WithValidate from './HOCs/WithValidate';
import { VerifyStep } from "./Services/StepValidations";
import NavigationButtons from "./Components/Layouts/NavigationButtons";

const Location = WithValidate(D, 0);
const TakeOrder = WithValidate(A, 1);
const ConfirmOrder = WithValidate(B, 2);
const ContactData = WithValidate(C, 3);
const Payment = WithValidate(E, 4);
const Done = WithValidate(F, 5);

const STEPS = [
  { t: "Ingresa tu ubicación", resumido: "Ubicación", icon: PlaceOutlined, currentIcon: Place },
  { t: "Elige tus productos", resumido: "Elige", icon: AddShoppingCartOutlined, currentIcon: ShoppingCart },
  { t: "Confirma tu pedido", resumido: "Confirma", icon: ReceiptOutlined, currentIcon: Receipt },
  { t: "Datos de Contacto", resumido: "Contacto", icon: PersonOutlined, currentIcon: Person },
  { t: "Método de Pago", resumido: "Pago", icon: MonetizationOnOutlined, currentIcon: MonetizationOn },
];

const App = withRouter(class extends React.Component {
  state = new State();
  constructor(props) {
    super(props);
    this.handleItemChange = this.handleItemChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleInvoiceDataChange = this.handleInvoiceDataChange.bind(this);

    this.handleMount = this.handleMount.bind(this);
    this.handleNext = this.handleNext.bind(this);
    this.handleBack = this.handleBack.bind(this);

    this.handleDataChange = this.handleDataChange.bind(this);
    if (prefs.Get("contactData")) {
      this.state.contactData = prefs.Get("contactData");
    }

    this.state.store = props.store;
    // la direccion viene pre-cargada!
    if (props.direccion) {
      let msg = VerifyStep(0, { ...this.state, direccion: props.direccion });
      if (!msg) {
        this.state.direccion = props.direccion;
        props.history.push("/1");
      }
    }
  }
  PROMISE_GUARDAR;
  handleMount(step) {
    if (this.state.orderNumber && step !== 5) {
      // CUANDO SE MUEVE EL PEDIDO, POR EJEMPLO HACIA ATRAS, BORRO TODO Y COMIENZO DE 0
      const state = { ...new State(), loading: false, productList: [...this.state.productList] };
      state.contactData = prefs.Get("contactData") || {};
      state.invoiceData = null;
      state.delivery = null;
      this.setState(state);
      this.props.history.push("/" + state.activeStep);
      return 0;
    }
    // verifica que los datos esten completos antes de pasar al siguiente paso
    var prevstep = step - 1;
    var goToStep = undefined;
    if (step >= 0) {
      while (prevstep >= 0) {
        let msg = VerifyStep(prevstep, this.state);
        if (msg) goToStep = prevstep;
        prevstep--;
      }
    }
    if (goToStep >= 0)
      return this.props.history.push("/" + goToStep);

    // verifica que los datos asincronicos esten cubiertos antes de pasar el siguiente paso
    // tambien obtiene la información que este proximo paso necesite
    if (step > 0) {
      // mejora en la validacion, pero bueno!
      if (!this.state.delivery) {
        console.info("No hay costo de envio, verificando la cobertura");
        return this.VerificarUbicacion();
      }
      if ((step === 1 || step === 2) && !this.state.productList.length) {
        console.info("No hay productos en la lista, obteniendo productos");
        return this.LoadProducts();
      }
    }
    if (step === 5) {
      // cuando se abre el paso 5, la primera accion es GUARDAR todo
      if (this.state.orderNumber || this.state.loading) return;
      if (this.PROMISE_GUARDAR) return;

      this.setState({ loading: "guardar", errGuardar: null });
      this.PROMISE_GUARDAR = API.SaveOrder(this.state).then(order => {
        this.setState({ loading: false, orderNumber: order.numero, errGuardar: null }, () => {
          this.PROMISE_GUARDAR = null;
        });
      }).catch(ex => {
        this.setState({ loading: false, errGuardar: ex }, () => {
          this.PROMISE_GUARDAR = null;
        });
      });

    }
    this.setState({ activeStep: step });
  }
  handleDataChange(prop, value) {
    this.setState(prev => {
      const state = { ...prev };
      if (prop === "direccion") {
        // cuando la direccion CAMBIA, el objeto delivery se borra
        var prev_position = state[prop].position;
        var new_position = value ? value.position : null;
        // si antes no habia posicion, o ahora tampoco hay, no deberia existir el objeto delivery
        if (!new_position || !prev_position) {
          if (state.delivery) {
            console.info("App.State borrando DELIVERY:", state.delivery);
          }
          state.delivery = null;
        } else {
          // Si las coordinadas son distintas, deberia borrarse el delivery tambien
          if (prev_position.lat !== new_position.lat || prev_position.lng !== new_position.lng) {
            if (state.delivery) {
              console.info("App.State borrando DELIVERY:", state.delivery);
            }
            state.delivery = null;
          }
        }
      }
      state[prop] = value;
      return state;
    });
  }

  LoadProducts() {
    if (this.state.productList.length) {
      console.warn("Load Products LOADED MORE THAN ONCE");
      return;
    }
    this.setState({
      loading: true,
      loadingMessage: "Obteniendo los productos de la tienda...",
      error: null
    });
    return API.GetProducts().then(products => {
      let productList = products.map(item => {
        var pvp_final = 0;
        if (item.pvp) pvp_final = item.pvp;
        if (item.pagaIva && this.state.store.porcentajeIVA)
          pvp_final += (item.pvp * (+this.state.store.porcentajeIVA) / 100);
        item.pvp_final = pvp_final;
        return item;
      });
      this.setState({
        productList,
        loading: null,
        loadingMessage: null
      }, () => console.info("Se han cargado los productos al estado"))
    }).catch(ex => {
      console.dir(ex);
      if (ex.status === 404) return this.setState({
        loading: null, loadingMessage: null, productList: [],
        error: { message: "Esta tienda no tiene ningun articulo publicado." }
      });
      this.setState({
        loading: null, loadingMessage: null, error: { ...ex }
      });
    });
  }
  VerificarUbicacion() {
    const { direccion } = this.state;
    this.setState({
      loading: true,
      loadingMessage: "Verifcando el área de cobertura. Espera un momento.",
      error: null,
      delivery: null
    });
    return API.CheckCoverage(direccion.position)
      .then(results => {
        if (results.isCovered) {
          return this.setState({
            direccion: { ...direccion },
            delivery: {
              status: results.deliveryPriceStatus,
              valor: results.deliveryPrice,
              metros: results.distance,
              segundos: results.duration,
              info_id: results.info_id
            },
            loading: null,
            loadingMessage: null
          });
        }
        var error;
        if (results.nearestStore) {
          setTimeout(() => {
            API.RedirectTo(results.nearestStore,
              direccion);
          }, 100);
          this.setState({
            error: {
              message: "Estamos dirigiendote a la tienda más cercana a ti.",
              action: function () { },
              btnMessage: "ESPERA UN MOMENTO"
            },
            loading: null,
            loadingMessage: null
          });
        } else {
          this.setState({
            error: {
              message: "Esta tienda no está disponible para tu área. Intenta con otra ubicación.",
              btnMessage: "CAMBIAR UBICACIÓN",
              action: () => {
                this.props.history.push("/0");
                this.setState({ error: null, loading: null, loadingMessage: null });
              }
            }
          });
        }
      }).catch(error => this.setState({ error }));
  }

  handleInputChange(event) {
    const { name, value } = event.currentTarget;
    this.setState(prev => {
      const state = { ...prev };
      if (name.indexOf(".") >= 0) {
        const levels = name.split(".");
        const key = levels.pop();
        let reference = state;
        for (let i = 0; i < levels.length; i++) {
          reference = reference[levels[i]];
        }
        reference[key] = value;
      } else {
        state[name] = value;
      }
      return state;
    });
  }
  handleItemChange(item) {
    let PrevItem = this.state.items.find(x => x.id === item.id);
    if (!PrevItem && item.cantidad) {
      const items = [...this.state.items];
      items.push(item);
      return this.setState({ items });
    }
    if (PrevItem) {
      const items = [...this.state.items];
      let PrevIndex = items.indexOf(PrevItem);
      if (item.cantidad) {
        items[PrevIndex] = item;
      } else {
        items.splice(PrevIndex, 1);
      }
      this.setState({ items });
    }
  }
  handleInvoiceDataChange(invoiceData) {
    this.setState({ invoiceData });
  }
  handleNext() {
    const { contactData, direccion, activeStep } = this.state;

    var deniedReason = VerifyStep(activeStep, this.state);
    if (deniedReason) return alert(deniedReason);

    if (activeStep === 0 && direccion) {
      const { gps_enabled, ...storable_data } = direccion;
      prefs.Set("direccion", JSON.stringify(storable_data));
    }
    if (activeStep === 3 && contactData)
      prefs.Set("contactData", JSON.stringify(contactData));

    this.props.history.push("/" + (this.state.activeStep + 1));
  }
  handleBack() {
    this.props.history.push("/" + (this.state.activeStep - 1));
  }


  render() {
    const {
      activeStep, productList, loading, loadingMessage, error, store,
      items, nota,
      forma_pago, numeroTransaccion,
      invoiceData, contactData,
      direccion,
      errGuardar, orderNumber
    } = this.state;
    return (
      <Paper style={{
        maxHeight: "100%",
        minHeight: "100%",
        margin: "auto",
        maxWidth: 700,
        display: "flex",
        flexDirection: "column"
      }} >

        <Stepper className="hide-on-min-height" alternativeLabel activeStep={activeStep}>
          {STEPS.map((x, i) => generateStep(x, i, activeStep))}
        </Stepper>

        <Typography variant="h6" className="show-on-min-height" style={{
          textAlign: "center",
          textTransform: "uppercase",
          background: "#3f51b5",
          paddingTop: 5,
          paddingBottom: 5,
          color: "white"
        }}>
          {(STEPS[activeStep] || {}).t}
        </Typography>

        <div style={{ flex: 1, overflow: "hidden", display: "flex", flexDirection: "column" }}>
          {error
            ? (<div style={{ flex: 1, textAlign: "center", padding: 20 }}>
              <Typography variant="h6" color="secondary">{error.message}</Typography>
              <Button fullWidth style={{ marginTop: 20, marginBottom: 20 }} onClick={() => { error.action ? error.action() : window.location.reload() }} variant="outlined" color="primary">
                {error.btnMessage || "REINTENTAR"}
              </Button>
            </div>)
            : (loading
              ? <div style={{ flex: 1, textAlign: "center", padding: 20 }}>
                <CircularProgress size={40} />
                <Typography color="primary" variant="h6">{loadingMessage}</Typography>
              </div>
              : (<React.Fragment>
                {loading && <Prompt message="Hay un proceso en progreso, no deberia salir hasta que se complete. Está seguro de salir?" />}
                <Switch>
                  <Route path="/0" render={props => <Location
                    {...props}
                    style={{ flex: 1 }}
                    store={store}
                    onDataChange={this.handleDataChange}
                    onMount={this.handleMount}
                    data={direccion}
                    prop={"direccion"}
                  />}
                  />


                  <Route path="/1"
                    render={props => <TakeOrder
                      {...props}
                      style={{ flex: 1 }}
                      onMount={this.handleMount}
                      onItemChange={this.handleItemChange}
                      choosenItems={items}
                      productList={productList}
                    />}
                  />


                  <Route path="/2"
                    render={props => <ConfirmOrder
                      {...props}
                      style={{ flex: 1 }}
                      productList={productList}
                      delivery={this.state.delivery}
                      envio_programado={this.state.envio_programado}
                      onMount={this.handleMount}
                      onItemChange={this.handleItemChange}
                      onInputChange={this.handleInputChange}
                      onDataChange={this.handleDataChange}

                      items={items}
                      nota={nota}
                      store={store}
                    />}
                  />

                  <Route path="/3"
                    render={props =>
                      <ContactData
                        {...props}
                        requireContactCedula={store && store.requireContactCedula}
                        onMount={this.handleMount}
                        invoiceData={invoiceData}
                        data={contactData}
                        onInputChange={this.handleInputChange}
                        onInvoiceDataChange={this.handleInvoiceDataChange}
                      />}
                  />

                  <Route path="/4"
                    render={props => (<Payment
                      {...props}
                      onMount={this.handleMount}
                      onInputChange={this.handleInputChange}
                      numeroTransaccion={numeroTransaccion}
                      forma_pago={forma_pago}
                      store={store}
                    />)} />

                  <Route path="/5"
                    render={props => (<Done
                      {...props}
                      ws_msg={store.ws_msg}
                      ws_phone={store.ws_phone}
                      contactData={contactData}
                      orderNumber={orderNumber}
                      onMount={this.handleMount}
                      loading={loading}
                      error={errGuardar}
                      saved={Boolean(orderNumber)}
                    />)} />


                  <Redirect exact from="/" to="/0"></Redirect>
                  <Redirect exact from="" to="/0"></Redirect>
                </Switch>


                {STEPS.length > this.state.activeStep && <NavigationButtons
                  onNextClick={this.handleNext}
                  onBackClick={this.handleBack}
                />}
              </React.Fragment>))}
        </div>
      </Paper >
    );
  }
});

function generateStep(item, index, current) {
  let data = {};
  if (current === index) data = { icon: <item.currentIcon /> };
  if (index > current) data = { icon: <item.icon /> };
  return (<Step key={index} >
    <StepLabel {...data} >{item.resumido}</StepLabel>
  </Step >)
}


export default function (props) {
  return (<HashRouter>
    <App {...props} />
  </HashRouter>);
}