import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Home from './Home';
import API from "./Services/API";

var PRE_LOADED_DIRECCION;
if (window.location.search && window.URLSearchParams) {
    const params = new URLSearchParams(window.location.search);
    var direccion = params && params.get("step1");
    try {
        direccion = atob(direccion) && JSON.parse(atob(direccion));
    } catch (ex) {
        direccion = null;
    }
    PRE_LOADED_DIRECCION = direccion;
}
API.GetStoreData()
    .then(store => {
        if (store.estado === "DISPONIBLE" && PRE_LOADED_DIRECCION) {
            return LaunchApp({ store, direccion: PRE_LOADED_DIRECCION });
        }
        return LaunchHome({ store, direccion: PRE_LOADED_DIRECCION });
    }).catch(error => {
        LaunchHome({ error })
    });


function LaunchApp(props) {
    ReactDOM.render(<App {...props} />, document.getElementById('app'));
}
function LaunchHome(props) {
    ReactDOM.render(<Home {...props} launch={LaunchApp} />, document.getElementById('app'));
}


